import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';
import { Report } from '../models/report';

@Injectable()
export class ReportsService {

  reportsCollection: AngularFirestoreCollection<Report>;
  reports: Observable<Report[]>;
  reportDoc: AngularFirestoreDocument<Report>;

  constructor(public afs: AngularFirestore) {
    this.reportsCollection = this.afs.collection('reports', ref=> ref.orderBy('date','desc'));
    this.reports = this.reportsCollection.snapshotChanges().map(changes=>{
      return changes.map(a=>{
        const data = a.payload.doc.data() as Report;
        data.id = a.payload.doc.id;
        return data;
      });
    });
  }

  getReports(){
    return this.reports;
  }

  addItem(report: Report){
    this.reportsCollection.add(report);
  }

  deleteReport(report: Report){

    this.reportDoc = this.afs.doc(`reports/${report.id}`);
    this.reportDoc.delete();

  }

}



